import Vue from 'vue'
import Router from 'vue-router'
import {getToken} from "utils/token.ts";
// 
import NotFound from '@/views/404.vue'
import MobilePrompt from '@/views/MobilePrompt.vue'
import StudentLogin from '@/views/login/StudentLogin.vue'
import Register from '@/views/register/Register.vue'
import ForgetPassword from '@/views/login/ForgetPassword.vue'
import Admin from '@/views/admin/Admin.vue'
import SetProfessions from '@/views/admin/enrollment/SetProfessions.vue'
import JuniorAll from '@/views/admin/check/JuniorAll.vue'
import JuniorFailed from '@/views/admin/check/JuniorFailed.vue'
import JuniorPassed from '@/views/admin/check/JuniorPassed.vue'
import SeniorAll from '@/views/admin/check/SeniorAll.vue'
import SeniorFailed from '@/views/admin/check/SeniorFailed.vue'
import SeniorPassed from '@/views/admin/check/SeniorPassed.vue'
import EditMessage from '@/views/admin/publish/EditMessage.vue'
import Application from '@/views/student/apply/Application.vue';
import UploadAttachment from '@/views/student/apply/UploadAttachment.vue';
import JuniorCheck from '@/views/student/check/JuniorCheck.vue';
import SeniorCheck from '@/views/student/check/SeniorCheck.vue';
import Student from '@/views/student/Student.vue';

Vue.use(Router);

const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'StudentLogin',
            component: StudentLogin,
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/register',
            name: 'Register',
            component: Register,
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/forget-password',
            name: 'ForgetPassword',
            component: ForgetPassword,
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/cannot-reach',
            name: 'CannotReach',
            component: MobilePrompt,
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/message',
            name: 'test',
            component: () => import('@/views/student/message/Message.vue'),
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/404',
            name: '404',
            component: NotFound,
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/admin',
            name: '',
            component: Admin,
            iconClass: 'el-icon-menu',
            hidden: true,
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/admin',
            name: '',
            component: Admin,
            iconClass: 'el-icon-date',//
            leaf: true,
            children: [
                {path: '/admin/set-profess', component: SetProfessions, name: ''}
            ],
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/admin',
            component: Admin,
            name: '',
            iconClass: 'el-icon-edit',
            children: [
                {path: '/admin/junior-not-pass-stu', component: JuniorFailed, name: ''},
                {path: '/admin/junior-passed-stu', component: JuniorPassed, name: ''},
                {path: '/admin/junior-all-stu', component: JuniorAll, name: ''}
            ],
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/admin',
            component: Admin,
            name: '',
            iconClass: 'el-icon-edit-outline',
            children: [
                {path: '/admin/senior-not-pass-stu', component: SeniorFailed, name: ''},
                {path: '/admin/senior-passed-stu', component: SeniorPassed, name: ''},
                {path: '/admin/senior-all-stu', component: SeniorAll, name: ''}
            ],
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/admin',
            component: Admin,
            name: '',
            iconClass: 'el-icon-message',
            leaf: true,
            children: [
                {path: '/admin/edit-msg', component: EditMessage, name: ''},
            ],
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/student',
            name: '',
            component: Student,
            iconClass: 'el-icon-menu',
            leaf: true,
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/student',
            name: '',
            component: Student,
            iconClass: 'el-icon-date',//
            children: [
                {path: '/student/application', component: Application, name: ''},
                {path: '/student/upload-attach', component: UploadAttachment, name: ''}
            ],
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '/student',
            component: Student,
            name: '',
            iconClass: 'el-icon-edit',
            children: [
                {path: '/student/junior-check', component: JuniorCheck, name: ''},
                {path: '/student/senior-check', component: SeniorCheck, name: ''}
            ],
            meta: {
                icon: '',
                title: ''
            }
        },
        {
            path: '*',
            redirect: '/',
        }
    ]
});

//  name
const LOGIN_PAGE_NAME = 'StudentLogin';

// 
router.beforeEach((to, from, next) => {
    // const token = getToken();
    if (to.meta.title) {
        document.title = to.meta.title
    }
    if (window.screen.width <= 600) {
        next({
            name: 'CannotReach'
        })
    } else {
        next();
    }
    // if (!token && to.name !== LOGIN_PAGE_NAME) {
    //     // 
    //     next({
    //         name: LOGIN_PAGE_NAME // 
    //     });
    // } else if (!token && to.name === LOGIN_PAGE_NAME) {
    //     // 
    //     next(); // 
    // } else if (token && to.name === LOGIN_PAGE_NAME) {
    //     // 
    //     next();// 
    // } else {
    //     if (token) {
    //         next(); // 
    //     } else {
    //         next({
    //             name: LOGIN_PAGE_NAME
    //         });
    //     }
    // }
});

// 
router.afterEach(to => {
    //
});

export default router;



