<template>
    <el-button type="text" @click="open"></el-button>
</template>

<script>
    export default {
        name: "SeniorCheck",
        methods: {
            open() {
                this.$alert('', '', {
                    confirmButtonText: '',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }
                });
            }
        }
    }
</script>


<style scoped>

</style>



